import React, { useState, useEffect } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import handleViewport from 'react-in-viewport';

import Title from './Title';

const Vaardigheden = ({ inViewport, innerRef, skill, index }) => {

    const [fade, setFade] = useState(false);

    useEffect(() => {
        if (inViewport && !fade) setFade(true);
    }, [inViewport]);

    return (
        <div ref={innerRef} className={`animated animatedFadeInUp ${fade ? 'fadeInUp' : ''} animationWait${index}`}>
            <Title id={`skill${index}`} customClass='vaardigheden-title' title={skill.title} />
            <div className='vaardigheden'>
                {skill.items.map((item, key) => (
                    <div key={key}>
                        <FontAwesomeIcon icon={item.icon} size='2x' />
                        <div className='text'>
                            {item.text}
                        </div>
                    </div>
                ))}
            </div>
        </div>
    )
};

export default handleViewport(Vaardigheden);;

