const App = () => {

  const [day, setDay] = useState(true);
  const [menu, setMenu] = useState('info');
  const initRefs = [];
  let refs = [];

  useEffect(() => {
    axios.get('https://api.sunrise-sunset.org/json?lat=52.1326&lng=5.2913')
      .then(res => {
        const data = res.data;
        if (data && data.results) {
          const sunrise = moment(data.results.sunrise, ['h:m a', 'H:m']);
          const sunset = moment(data.results.sunset, ['h:m a', 'H:m']);
          const now = moment();
          setDay(sunrise.diff(now) < 0 && sunset.diff(now) > 0);
        }
      });

    window.addEventListener('scroll', handleScroll);

    refs = [...initRefs];

    return () => {
      window.removeEventListener('scroll', handleScroll);
    }
  }, []);

  const handleScroll = () => {
    refs.forEach((ref, index) => {
      if (ref !== null) {
        const rect = ref.getBoundingClientRect();
        if (rect.y > -50 && rect.y < 50) {
          setMenu(['info', 'skills', 'projects'][index]);
        } else if (index === 3 && (window.innerHeight - rect.y) > -100) {
          setMenu('contact');
        }
      }
    })
  }
  
  return (
    <ParallaxProvider>
        <div id='app' className={day ? '' : 'dark'}>
            <div></div>
            <div id='app-inner'>
                <Header menu={menu} switchTheme={() => setDay(!day)}/>
                <Info propsRef={r => (initRefs[0] = r)} />
                <Title propsRef={r => (initRefs[1] = r)} id='vaardigheden' title='Vaardigheden' />
                <div id='vaardigheden-container'>
                  {skills.map((skill, key) => (
                    <Skill 
                      skill={skill} 
                      key={key} 
                      index={key} 
                    />
                  ))}
                </div>
                <Title propsRef={r => (initRefs[2] = r)} id='projecten' title='Mijn projecten' />
                {projects.map((project, key) => (
                  <Project 
                    project={project} 
                    key={key} 
                  />
                ))}
                <Title id='contact' title='Contact' />
                <Contact onEnterViewport={() => setMenu('contact')} />
                <div ref={r => (initRefs[3] = r)} id='footer'>
                    <FontAwesomeIcon icon={faCopyright}/>
                    {new Date().getFullYear()} Lucas Bos
                </div>
            </div>
            <div></div>
        </div>
    </ParallaxProvider>
  )
};

export default App;